<template>
	<view class="content">
		<u-row>
			<u-col span="12" style="text-align: center;min-height: 104rpx;padding-top: 24rpx;">
				<u-search shape="round" v-model="keyword" @search="Tosearch" @custom="Tosearch"></u-search>
			</u-col>
		</u-row>
		<scroll-view scroll-y style="height: 920rpx;">
			<view class="list01" v-for="(item,index) in zxlist" :key="index" @click="details(item.id)">
				<u-row>
					<u-col span="8">
						<view class="blueline"></view>
						<span>{{item.universalAntiSeekAdviceMain97}}</span>
						<p>{{item.createTime}}</p>
					</u-col>
					<u-col span="4" style="text-align: center;">
						<image :src="item.file" mode="center" class="listimg"></image>
					</u-col>
				</u-row>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import {
		listObj,
		getDicts
	} from '../../api/index.js'
	export default {
		data() {
			return {
				params: {
					pageNum: 1,
					pageSize: 10
				},
				search: 1,
				keyword: '',
				show: 1,
				form: {},
				current: 0,
				swiperCurrent: 0,
				list: [{
					name: '反恐资讯'
				}, {
					name: '活动动态'
				}],
				list01: [{
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, ],
				zxlist: [],
			}
		},
		onLoad() {
			if (this.$route.query.keyword) {
				this.keyword = this.$route.query.keyword;
				this.Tosearch();
			} else {
				this.listall();
			}
		},
		methods: {
			details(id) {
				uni.navigateTo({
					url: 'details?id=' + id
				})
			},
			listall() {
				listObj(this.params).then(res => {
					if (res.code == 200) {
						this.zxlist = res.rows;
					}
				})
			},
			Tosearch() {
				if (this.keyword) {
					this.params.queryJson = [{
						data1: 'universalAntiSeekAdviceMain97',
						data2: '2',
						data3: this.keyword,
						objectId: "294",
						subType: null,
						flag: false,
						fileType: "String"
					}];
					this.params.queryJson = JSON.stringify(this.params.queryJson);
					this.listall();
				}
			},
			tabsChange(index) {
				this.swiperCurrent = index;
			},
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.uTabs.setDx(dx);
			},
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.uTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		background: url('@/static/img/bg.png') no-repeat center center fixed;
		background-size: cover;
		box-sizing: border-box;
		padding: 0 40rpx;
	}

	/deep/ uni-swiper {
		min-height: 570rpx;
	}

	.topBox {
		position: relative;
		font-size: 32rpx;
		font-weight: bold;
		color: #333333;
	}

	.topBox .ss {
		position: absolute;
		right: -10rpx;
		top: 8rpx;
		width: 30rpx;
		height: 30rpx;
	}

	.grid-text {
		font-size: 24rpx;
		font-weight: bold;
		color: #333333;
		margin-top: 16rpx;
	}

	.list01 {
		width: 100%;
		height: 172rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 20rpx 30rpx;
		position: relative;
	}

	.blueline {
		position: absolute;
		left: 0;
		top: 30rpx;
		width: 6rpx;
		height: 28rpx;
		background: #0062E9;
	}

	.list01 span,
	.list02 span {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}

	.list01 p,
	.list02 p {
		margin-top: 20rpx;
		font-size: 24rpx;
		color: #999999;
		width: 90%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 32rpx;
		top: 20rpx;
	}

	.listimg {
		width: 208rpx;
		height: 126rpx;
	}

	.list02 {
		width: 100%;
		height: 231rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 17rpx 30rpx;
		position: relative;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
	}

	.bz01 {
		width: 52rpx;
		vertical-align: middle;
		display: inline-block;
		margin-left: -10rpx;
	}

	.bz02 {
		width: 28rpx;
		margin-right: 6rpx;
		vertical-align: middle;
		display: inline-block;
	}
</style>